<template>
  <div class="productList">
    <div class="productList-title">
      <p>甘露醇注射液 - 上海百特医疗用品有限公司</p>
    </div>
    <div class="productList-message">
        <div class="name">
            <h6>药品名称:</h6>
            <p>通用名称：甘露醇注射液</p>
            <p>英文名称：Mannitol Injection</p>
            <p>商品名称：甘露醇注射液</p>
        </div>
        <div class="name">
            <h6>成份:</h6>
            <p>本品主要成分及化学名称：D-甘露糖醇</p>
            <p>辅料：如有需要，适量氢氧化钠调节PH值</p>
        </div>
        <div class="name">
            <h6>适应症:</h6>
            <p>组织脱水药。用于治疗各种原因引起的脑水肿，降低颅内压，防止脑疝</p>
            <p>降低眼内压。可有效降低眼内压，应用于其他降眼内压药无效时或眼内手术前准备</p>
            <p>渗透性利尿药。用于鉴别肾前性因素或急性肾功能衰竭引起的少尿。亦可应用于预防各种原因引起的急性肾小管坏死</p>
            <p>作为辅助性利尿措施治疗肾病综合征、肝硬化腹水，尤其是当伴有低蛋白血症时</p>
            <p>对某些药物逾量或毒物中毒（如巴比妥类药物、锂、水杨酸盐和溴化物等），本药可促进上述物质的排泄，并防止肾毒性</p>
            <p>作为冲洗剂，应用于经尿道内作前列腺切除术</p>
            <p>术前肠道准备</p>
        </div>
        <div class="name">
            <h6>用法用量:</h6>
            <p>利尿。常用量为按体重1～2g/kg，一般用20%溶液250m1静脉滴注，并调整剂量使尿量维持在每小时30～50m1</p>
            <p>治疗脑水肿、颅内高压和青光眼。按体重0.25～2g/kg，配制为15%～25%浓度于30～60分钟内静脉滴注。当病人衰弱时，剂量应减小至0.5g/kg。严密随访肾功能</p>
            <p>鉴别肾前性少尿和肾性少尿。按体重0.2g/kg，以20%浓度于3～5分钟内静脉滴注，如用药后2～3小时以后每小时尿量仍低于30～50ml，最多再试用一次，如仍无反应则应停药。已有心功能减退或心力衰竭者慎用或不宜使用</p>
            <p>预防急性肾小管坏死。先给予12.5～25g，10分钟内静脉滴注，若无特殊情况，再给50g，1小时内静脉滴注，若尿量能维持在每小时50m1以上，则可继续应用5%溶液静滴; 若无效则立即停药</p>
            <p>治疗药物、毒物中毒。50g以20%溶液静滴，调整剂量使尿量维持在每小时100～500ml</p>
            <p>肠道准备。术前4～8小时，10%溶液1000ml于30分钟内口服完毕</p>
        </div>
        <div class="name">
            <h6>不良反应:</h6>
            <p>冲洗液通过被切开的静脉被人体吸收，可导致血液循环的负担过重，心脏衰竭或电解质紊乱，对有原发性肾或心、肺疾病患者进行较长时间的前列腺电切手术时，则尤为突出</p>
            <p>器质性肾病患者超剂量使用时，将导致水中毒，可出现中枢神经系统过度兴奋，如精神紊乱、发冷、恶心，有时会出现神经末梢痉挛</p>
            <p>超剂量使用，可引起血液稀释，导致电解质紊乱</p>
        </div>
        <div class="name">
            <h6>禁忌:</h6>
            <p>本品禁用于静脉注射，也不可口服</p>
        </div>
        <div class="name">
            <h6>警告:</h6>
            <p>尚不明确</p>
        </div>
        <div class="name">
            <h6>批准文号:</h6>
            <p>甘露醇注射液 100ml:20g 国药准字 H20073135</p>
            <p>甘露醇注射液 250ml:50g 国药准字 H20003300</p>
        </div>
        
        
    </div>
    



  </div>
</template>

<script>
import { yaoClass } from "~/data/yaoClass";

export default {
  data() {
    return {
      yaoClass: yaoClass,
      id: "",
      id1: ""
    };
  },
  created() {
    this.id = this.$route.params.id;
    this.id1 = parseInt(this.id);
  }
};
</script>

<style lang="scss" scoped>
.productList {
  .productList-title {
    background-color: #461b93;
    color: #fff;
    text-align: center;
    p {
      line-height: 100px;
      font-weight: bold;
    }
  }
  .productList-message {
    padding: 15px;
    .name {
      border-bottom: 1px solid #eee;
      padding: 15px 0;
      h6 {
          margin: 10px 0;
        color: #545454;
        font-size: 15px;
        font-weight: bold;
        overflow: hidden;
      }
      p {
        color: #5f5f5f;
        font-size: 14px;
        line-height: 22px;
      }
    }
  }
}
</style>



